@media only screen and (min-width: 768px) {
  .header-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    padding: 2vh 5vw;
  }

  .App {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }  

  .profile-picture {
    background: url('https://github.com/ThiagoDRangel.png');
    background-size: cover;
    background-position: center;
    width: 12vw;
    height: 25vh;
    border-radius: 50%;
    padding: 10%;
    border: 2px solid #ccc;
    box-sizing: border-box;
    margin-right: 2vw;
  }

  .titles-container {
    align-items: center;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .title {
    font-size: 3rem;
    margin-bottom: 1rem;
  }

  .subtitle {
    font-size: 2rem;
    font-weight: 300;
    margin-bottom: 1rem;
  }

  .button-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 30vh;
  }

  .button-container:hover {
    cursor: pointer;
  }

  .link-button {
    background-color: white;
    border: none;
    border-radius: 10px;
    margin-right: 1vw;
  }

  .link-button:hover {
    background-color: #eee;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }

  .icon {
    font-size: 2rem;
  }

  footer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5vh;
    font-size: 1rem;
  }

  .span-footer {
    margin-right: 0.5rem;
  }
}

@media only screen and (max-width: 767px) {
  .header-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .profile-picture {
    background: url('https://github.com/ThiagoDRangel.png');
    background-size: cover;
    background-position: center;
    width: 40vw;
    height: 40vw;
    border-radius: 50%;
    padding: 10%;
    border: 2px solid #ccc;
    box-sizing: border-box;
  }

  .titles-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .title {
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  .subtitle {
    font-size: 1.5rem;
    font-weight: 300;
    margin-bottom: 1rem;
  }

  .button-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 1vw;
  }
  
  .icon {
    font-size: 2rem;
    margin-right: 0.5vw;
  }
  
  .link-button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: white;
    text-decoration: none;
    color: #222;
    font-size: 1.2rem;
  }
  

  .button-container:hover {
    cursor: pointer;
  }

  .icon {
    font-size: 2rem;
  }

  footer {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
  }

  .span-footer {
    margin-right: 0.5rem;
  }
}
